<template>
  <div class="home">
    <!-- 头部 -->
    <div class="home-header">
      <van-sticky class="header-search">
        <van-row>
          <van-col span="3">
            <img src="../assets/images/letaogou.png" alt />
          </van-col>
          <van-col span="18">
            <van-search
              @focus="getSearch"
              shape="round"
              background="#036856"
              placeholder="请输入搜索关键词"
            />
          </van-col>
          <van-col span="3">
            <router-link to="/user">
              <van-icon name="manager-o" color="#ffffff" />
            </router-link>
          </van-col>
        </van-row>
        <!-- 首页导航 -->
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div
              class="swiper-slide"
              v-for="(item,index) in navlist"
              :key="index"
              @click="getsecondary(item,index)"
            >{{item.title}}</div>
          </div>
        </div>
        <router-link tag="div" to="/classification" class="home-class">
          <van-icon name="bars" />
        </router-link>
      </van-sticky>
    </div>
    <!-- 二级路由插座 -->
    <router-view />
  </div>
</template>

<script>
import Swiper from "swiper";
export default {
  data() {
    return {
      index: 0,
      indexs: " ",
      navlist: [
        { title: "首页", name: "homepage" },
        { title: "电脑办公", name: "secondary" },
        { title: "食品", name: "secondary" },
        { title: "手机", name: "secondary" },
        { title: "运动", name: "secondary" },
        { title: "男装", name: "secondary" },
        { title: "女装", name: "secondary" },
        { title: "超市", name: "secondary" },
        { title: "图书", name: "secondary" },
        { title: "家电", name: "secondary" },
        { title: "大药房", name: "secondary"},
      ]
    };
  },
  created(){
    this.$store.state.vantTab = true;
  
  },
  methods: {
    
    getSearch() {
      this.$router.push({ name: "hstory" });
    },
    getsecondary(item, index) {
      if (item.name == "homepage") {
        this.$router.push({ name: item.name });
      } else {
        if (index === this.indexs) {
          return;
        } else {
          this.index = index;
          this.indexs = this.index;
          this.$router.push({ name: item.name, query: { activeName: index } });
        }
      }
    }
  },

  mounted() {
    new Swiper(".swiper-container", {
      slidesPerView: 6,
      freeMode: true
    });
  }
};
</script>

<style lang="less" scoped>
.home {
  > .home-header {
    height: 94px;
    > .header-search {
      img {
        width: 25px;
        height: 25px;
        position: absolute;
        top: 0px;
        bottom: 0px;
        left: 0px;
        right: 0px;
        margin: auto;
      }
      .home-class {
        width: 40px;
        height: 40px;
        position: absolute;
        right: 0;
        top: 54px;
        background-color: #036652;
        color: #ffffff;
        font-size: 25px;
        > .van-icon {
          width: 25px;
          height: 25px;
          position: absolute;
          top: 0px;
          bottom: 0px;
          left: 0px;
          right: 0px;
          margin: auto;
          cursor: pointer;
        }
      }
      .swiper-container {
        position: absolute;
        left: 0;
        top: 54px;
        width: calc(100% - 40px);
        height: 40px;
        background-image: url(../assets/images/homebg.jpg);
        background-size: 100% 400%;
      }
      .swiper-slide {
        text-align: center;
        font-size: 12px;
        color: #ffffff;
        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
      }
      .van-col {
        background-color: #036856;
        height: 54px;
        position: relative;
        font-size: 25px;
        .van-icon {
          width: 25px;
          height: 25px;
          position: absolute;
          top: 0px;
          bottom: 0px;
          left: 0px;
          right: 0px;
          margin: auto;
        }
      }
    }
  }

  > .fls-tit {
    width: 100%;
    height: 35px;
    > img {
      width: 100%;
      height: 100%;
    }
  }
  > .floor-container-outer {
    width: 100%;
    height: 260px;
    margin-bottom: 20px;
    > .floor-container {
      width: calc(100% - 20px);
      height: 100%;
      margin: 0 10px 10px 10px;
      border-radius: 5px;
      background-color: #ffffff;
      > .floor-container-item {
        width: 100%;
        height: 120px;
        text-align: center;
        padding: 10px 0 0 0;
        > a {
          display: inline-block;
          width: calc(25% - 1px);
          height: 100%;
          border-right: 1px solid #f7f7f7;
          color: black;
          > .item-title {
            font-size: 16px;
            font-weight: bold;
          }
          > span {
            font-size: 13px;
          }
          > img {
            display: block;
            width: 66px;
            height: 66px;
            margin: 6px 0 0 16px;
          }
        }
      }
      > .floor-container-item:nth-child(1) {
        height: 119px;
        border-bottom: 1px solid #f7f7f7;
      }
    }
  }
}
</style>